.header {
  padding: 20PX 0 35PX;
}

.header .title {
  position: relative;
  display: block;
  line-height: 44PX;
  text-align: center;
  font-size: 14PX;
  color: #999;
}

.header .title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 70PX;
  height: 0;
  border-bottom: 0.02133rem solid #ccc;
}

