page {
  background-color: #f8f8f8;
}

body {
  background-color: #f8f8f8;
}

.system__list {
  border-top: 0.02133rem solid #e5e5e5;
  border-bottom: 0.02133rem solid #e5e5e5;
  background-color: #fff;
}

.system__list-item {
  position: relative;
  height: 38PX;
  line-height: 38PX;
  font-size: 14PX;
}

.system__list-item:not(:last-child)::after {
  content: '';
  position: absolute;
  left: 15PX;
  right: 0;
  bottom: 0;
  border-bottom: 0.02133rem solid #e5e5e5;
}

.system__list-item-title {
  padding-left: 15PX;
  width: 10em;
  max-width: 10em;
  color: #333;
}

.system__list-item-content {
  color: #666;
}

.system__btn {
  margin: 20PX 15PX 0;
}

